import { useState,useEffect,useRef } from "react";
import { useNavigate} from "react-router-dom";
import {Preloader} from "../components/Preloader";


const PriceList = ()=>{

    const formatter = new Intl.NumberFormat("uk-UK",{minimumFractionDigits:2});
    const getKeysFromLocalStorageByStartsWith = (strStartKey)=>(Object.keys(localStorage).filter((key)=>(key.startsWith(strStartKey))));
    const keyLocalStorage = getKeysFromLocalStorageByStartsWith("stocks")[0];
    // const [data, setData] = useState(JSON.parse(localStorage.getItem(keyLocalStorage)));
    const [data, setData] = useState([]);
    const [searchWords, setSearchWords] =useState([]);
    const [searchStr, setSearchStr] =useState("");
	const stepPagination = 200;
	const [startRowShow, setStartRowShow]= useState(0);
	const [isPagination, setIsPagination] = useState(false);
    const inputRef = useRef(null);
    const navigate = useNavigate();
    const [isLoading, setIsLoading]= useState(true);
	

    

    useEffect(()=>{
		  setIsLoading(true);
        const data=JSON.parse(localStorage.getItem(keyLocalStorage));
        setData(data);
		  setIsLoading(false);
    },[])

	
    
    const filterData = data&&data.filter(item=>{
      return searchWords.every(fragment=>{
        return item[0].toLowerCase().includes(fragment.toLowerCase())
      })
    })

	useEffect(()=>{
		if(filterData.length>startRowShow+stepPagination) {
			setIsPagination(true)
		} else setIsPagination(false)

	},[filterData])

    const handleInput = (e)=>{
      setSearchStr(e.target.value);
      setSearchWords(e.target.value.split(" "));
		setStartRowShow(0)


    }

	const handlePagination =()=>{
		setStartRowShow(startRowShow+stepPagination);

	}

    const handleInputFocus =()=>{
        setTimeout(()=>{
            if(inputRef.current){
                inputRef.current.blur();
            }
        },8000)
    }
    
   


	// const SearchMark =({str, searchStr})=>{
  //       const index= str.toLowerCase().indexOf(searchStr.toLowerCase());
  //       if(index === -1) {
  //           return <div className="cell align-self-center mx-0 lh-1" style={{height:"30px", overflow:"hidden"}}>{str}</div>;
  //       }
  //       return (
  //           <div className="cell align-self-center mx-0 lh-1">
  //               {str.slice(0,index)}
  //               <span className="highlight">
  //                   {str.slice(index, index+searchStr.length)}
  //               </span>
  //               {str.slice(index+searchStr.length)}
  //           </div>
  //       )
  //   }

    const SearchMark3=({str, searchStr})=>{
      const searchWords = searchStr.split(" ").filter(word => word !== "");
      if(searchWords.length===0) return <div>{str}</div>
      const searchRegex = new RegExp(searchWords.join("|"), "gi");
      const result = str.replace(searchRegex, (match) => `<mark class="mark">${match}</mark>`);
      const markup ={__html:result}

      return (
        <div  dangerouslySetInnerHTML={markup}/>
      )
    }

    return (

        <div className="row mx-0 ">
            {/* <SearchMark3 str="Knauf Гипсокартон Влага 2500*1200*12,5 (56шт/палета)" searchStr="k 56 шт"/> */}
			
{/* 
			<div className="px-3  mx-auto" >
                <input
                    ref={inputRef}
                    id="input"
                    onChange={handleInput}
                    type="text"
                    style={{ fontSize: 18 ,width:"100%" }}
                    placeholder="Поиск по наименованию"
                    className="rounded-4 w-100 my-2 p-2 border-success" 
                    // onFocus={handleInputFocus}
                    />
                    
                   

            </div> */}
                   


			{isLoading?<Preloader/>:null}
            
 
                <table className="table table-sm mt-2 px-0 table-bordered table-hover"  >
				
                  <thead  >
                    <tr className="table-secondary " style={{position:"sticky",top:12}}>
                    
                            <th colSpan={3}  >
                                <div className="px-3  mx-auto" >
                                <input
                                    ref={inputRef}
                                    id="input"
                                    type="text"
                                    onChange={handleInput}
                                    // value = {filter}
                                    style={{ fontSize: 18 ,width:"100%" }}
                                    placeholder="Пошук за найменуванням"
                                    className="rounded-4 w-100 my-2 p-2 border-success" 
                                    // onFocus={handleInputFocus}
                                />
                                </div>
                            </th>
                           

                    </tr>

                    <tr className="table-secondary " style={{position:"sticky",top:72}}>
                        <th className="col-9 text-center align-items-center lh-2 fw-normal ">
                            <div className="cell ">Найменування</div>
                        </th>
                        <th className="col-3 text-center  lh-1 fw-normal">
                            <div className="cell">Цiна, Залишок</div>
                        </th>
                        {/* <th className="col-2 text-center align-items-center lh-1 fw-normal">
                            <div className="cell fs-6">Отобр., ед.изм</div>
                        </th> */}
                    </tr>
                  </thead>
                  <tbody >
                 
                  

                    {filterData&&
                      filterData.slice(0,startRowShow+ stepPagination).map((row) => (
                        <tr key={row[2]}  >
                          <td className="col-9 text-center align-items-center lh-1 fs-6 fw-normal" >
							<SearchMark3 str={row[0]} searchStr={searchStr}/>
                           
                          </td>
                          <td className="col-3 text-center align-items-center lh-1 fw-normal">
                            <div className="cell align-self-center">
                              <p className="text-danger mb-0 fs-6">{formatter.format(row[3])}</p>
                              <p className="mb-0 text-success fs-6 text-end">{row[1]} {row[4]}</p> 
                            </div>
                          </td>
                          {/* <td className="col-2 text-center align-items-center">
                            <div className="cell align-self-center">
                              {""}
                              <p>{""}</p> 
                             </div>
                          </td> */}
                   

                        </tr>
                      ))}

					  {isPagination?(
						<tr 	>
							<td colSpan={3}>
								<div className="d-flex justify-content-end">
									<button
										type="submit"
										style={{zIndex: 9999 }}
										onClick={handlePagination}
										cursor="pointer"
										className=" rounded-3 shadow-sm border border-success fs-1"
										>
										Загрузить еще
									</button>
								</div>
							</td>
						</tr>

					  ):null}
					  
                  </tbody>
				  
                </table>
				
  
              <footer className="footer fixed-bottom border-top">
                <div className="container">
                  <div className="row">
                    <div className="col-6 my-2 text-center">
                      <button
                        type="submit"
                        onClick={()=>navigate("/")}
                        className="rounded-3 shadow-sm border border-success fs-1"
                      >
                        Назад
                      </button>
                    </div>
                    <div className="col-6 my-2 text-center">
                      {/* <button
                        type="submit"
                        className="rounded-3 shadow-sm border border-success fs-1"
                      >
                        Вперед
                      </button> */}
                    </div>
                  </div>
                </div>
              </footer>
            </div>
        
    )
}

export {PriceList}