import { useLocation, useNavigate } from 'react-router-dom';
import {useState}from "react"
import { getDataByUserBarCodeAndOperation} from "../api_fake"
// import {getDataByUserBarCodeAndOperation} from "../api"

import { useAuth } from '../hook/useAuth'
import logo192 from "../images/logo192.png"

import {Preloader} from "../components/Preloader"
import {ToastContainer,toast} from "react-toastify"


const LoginPage = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const {signin} = useAuth();
    const fromPage = location.state?.from || '/scenarios';

    const [isLoading, setIsLoading] = useState(false);
    
    const [data, setData] = useState({});

    const [barCode, setBarCode]=useState("");

    const handlerBarCode =(e)=>{
        setData({})
        setBarCode(e.target.value);
    }

    const notify=(message)=>{
        toast.info(message);
    }
// ================================
    async function update(){
        try{
            setIsLoading(true);
            const res = await getDataByUserBarCodeAndOperation(barCode,"СписокСценариев");
            setIsLoading(false);
            if(res.User&&res.User.length>0){
               
                signin(barCode, () => navigate(fromPage, {replace: false}));

                localStorage.setItem("whoIs", JSON.stringify({"barCode":barCode,"userName":res.User}))
            }
            await setData(res);

        } catch (err){
            toast.error("Ошибка с интернетом: ",err);
            console.log("",err);
            setIsLoading(false);
        }
    }



    const result =(
            <>
                <div>
                    {data.MessageТext&&data.MessageТext.length>0?notify(data.MessageТext):null}

                    <div className="row justify-center-md-center mx-auto">
                        <div className="col mx-auto text-center center-block">
                            <div className="my-4">
                                <input type="image" src={logo192} alt="logo" className="rounded-3 shadow-sm" height="300px"  />

                            </div>
                            <div className="my-4">
                                <input className="rounded-3 shadow-sm fs-2" type="number" name="login"  placeholder="Отсканируйте бейдж" value={barCode} onChange={handlerBarCode}/>
                            </div>
                            <div className="my-3">
                                <button  onClick={update} type="submit" className="rounded-3 shadow-sm border border-success fs-1">Авторизуйтесь</button>
                            </div>
                            {isLoading?<Preloader/>:null}
                            <ToastContainer
                                position="top-center"
                                autoClose={4000}
                                hideProgressBar={false}
                                newestOnTop={false}
                                closeOnClick
                                rtl={false}
                                pauseOnFocusLoss
                                draggable
                                pauseOnHover
                                theme="colored"
                                />
                        </div>    
                    </div>   
                </div>   
               
            </>
    )

   
  return result
    
}

export {LoginPage};
