import {  useState } from "react";
import {Modal, Button, } from "react-bootstrap";
import { getStocksCrypted, decryptedJson, parseDate, getKeysFromLocalStorageByStartsWith } from "../api";
import { ToastContainer, toast } from "react-toastify";
import {Preloader} from "./Preloader"

export default function DecodeData({onHide, setIsVisibleBtnUpdate,setDateUpdateStocks,setIsVisibleBtnToForward,setShowClearLocalStorage, ...props}){
    const[code, setCode]=useState("");
    const[isLoading,setIsLoading]= useState(false);

    const handleInputChange = (e)=>{
        // const value = e.target.value;
        setCode(e.target.value);
    }

    const onClose=()=>{
        onHide(false);
    }

  
    const update = async () =>{
        setIsLoading(true);
        try {
            const result = await getStocksCrypted();
            const resultJSON = JSON.parse(result)
            const dateOfnewStocks = parseDate(resultJSON.dateOfPrice.slice(-10));
            const data = JSON.parse(decryptedJson(resultJSON.price,code));
            // console.log(data)
            if(data){
                localStorage.removeItem(getKeysFromLocalStorageByStartsWith("stocks")[0])
                localStorage.setItem(`stocks_${dateOfnewStocks}`,JSON.stringify(data));

                onHide(true);
                setIsVisibleBtnUpdate(false);
                setIsVisibleBtnToForward(true);
                setShowClearLocalStorage(true);
                setDateUpdateStocks(dateOfnewStocks.toLocaleString())
            }

            if (result){
                setIsLoading(false);
            } else{
                throw new Error("Invalid response")
            }
        
        } catch (error) {
            setIsLoading(false)
            toast.warning("Помилка, неправильний код. Спробуйте ще");
            setCode("");
        }
    }


  return(
  <Modal  {...props}  backdrop="static" aria-labelledby="contained-modal-title-vcenter"  centered    >
    <ToastContainer
                                position="top-center"
                                autoClose={4000}
                                hideProgressBar={false}
                                newestOnTop={false}
                                closeOnClick
                                rtl={false}
                                pauseOnFocusLoss
                                draggable
                                pauseOnHover
                                theme="colored"
                                />
  
    <Modal.Header >
      <Modal.Title id="contained-modal-title-vcenter" >
        <div className="text-center mx-auto justify-content-center"><span>Залишки закодовані.<br/> Щоб розкодувати, введіть код</span></div>
      </Modal.Title>
    </Modal.Header>
    {isLoading?<Preloader/>:null}
    <Modal.Body>
    <div className="col-6 px-0 mx-auto">
    <input className="fs-3 text-center" type="number"
    placeholder={"введiть код"}
      onChange={handleInputChange}
      value={code}
      onFocus={e=>{e.target.select()}}
    ></input>
    </div>
    </Modal.Body>
    <Modal.Footer>
    <Button onClick={update} >Завантажити</Button>

      <Button onClick={onClose}>Закрыть</Button>

    </Modal.Footer>
  </Modal>
)


}