import { useNavigate } from "react-router-dom";
import logo192 from "../images/logo192.png"
import { useState,useEffect } from "react";
import { getStocks, getDateUpdate } from "../api";
import { Preloader } from "../components/Preloader";
import DecodeData from "../components/DecodeData";



const Homepage = () => {
    const [isLoading, setIsLoading]= useState(false);
    const [isVisibleBtnUpdate,setIsVisibleBtnUpdate] = useState(false)
    const [isVisibleBtnToForward,setIsVisibleBtnToForward] = useState(false)

    // const [showButton, setShowButton] = useState(false);
    const [showClearLocalStorage, setShowClearLocalStorage] = useState(false);
    const [dateUpdateStocks, setDateUpdateStocks] = useState("");
    const navigate = useNavigate();
    const [btnTitle,setBtnTitle] = useState("Доступні свіжіші залишки. Оновити?");
    const [modalShow, setModalShow] = useState(false);


    

    useEffect(()=>{
        getFrashDateUpdate();
        const dateOfUpdateLocalStorage=getKeysFromLocalStorageByStartsWith("stocks")[0]&&new Date(getKeysFromLocalStorageByStartsWith("stocks")[0].replace("stocks_","")).toLocaleString();
        setDateUpdateStocks(dateOfUpdateLocalStorage);
        setShowClearLocalStorage(dateOfUpdateLocalStorage&&true)
    },[])


    const getFrashDateUpdate = async () =>{
            try {
                const result = await getDateUpdate();
                setIsLoading(false);
                if(result){
                    const dateOfnewStocks = parseDate(result.dateUpdate.slice(-10));
                    const keys = getKeysFromLocalStorageByStartsWith("stocks");
                    if(keys.length===0) {
                        setBtnTitle("Завантажте залишки.")
                        setIsVisibleBtnUpdate(true);
                    }

                    if(keys.length>0) {
                        if(dateOfnewStocks - new Date( keys[0].replace("stocks_",""))>0){
                            setIsVisibleBtnUpdate(true);
                        }
                        setIsVisibleBtnToForward(true)
                    }
                }
                
            } catch (error) {
                setIsLoading(true);
                console.log(error)
            }

    }

    const update = async ()=>{
        setIsLoading(true);
        try {
            const result = await getStocks();
           
            if(result){
                // const dateOfnewStocks = parseDate(result.dataOfPrice.slice(-10));
                // const keys = getKeysFromLocalStorageByStartsWith("stocks");
                   
                //     // console.log("There is already data in localStorage with key Stocks");
                //     if(dateOfnewStocks - new Date( keys[0].replace("stocks_",""))>0){
                //         setShowButton(true);
                //         // console.log("There are more new data can be updated")
                //     }
                setIsLoading(false);
        
                
            } else{
                throw new Error("Invalid response")
            }
            
        } catch (error) {
            setIsLoading(false)
            console.log(error)
        }

    }

    

    // const updateLocalStorageStocks = ()=>{
        
    //     getStocks();
    //     data = getData();
    //     console.log("data ",data)

    //     if(data){
    //         const dateOfnewStocks = parseDate(data&&data.dataOfPrice.slice(-10));
    //         // console.log(dateOfnewStocks);
    //         localStorage.removeItem(getKeysFromLocalStorageByStartsWith("stocks")[0])

    //         localStorage.setItem(`stocks_${dateOfnewStocks}`,JSON.stringify(data.price));
    //         setShowClearLocalStorage(true);
    //         setDateUpdateStocks(dateOfnewStocks.toLocaleString())
    //         setShowButton(false);
    //     }
    // }

    const updateStocks = () => {
        setModalShow(true);
    }

    const parseDate = (strDate)=>{
        const parts =strDate.split(".");
        const year =parseInt(parts[2]);
        const month =parseInt(parts[1])-1;
        const day = parseInt(parts[0]);
        return new Date(year, month, day);

    }
     
    const getKeysFromLocalStorageByStartsWith = (strStartKey)=>(Object.keys(localStorage).filter((key)=>(key.startsWith(strStartKey))));
    

    const clearLocalStorage = ()=>{
        localStorage.clear();
        setShowClearLocalStorage(false);
        setIsVisibleBtnToForward(false);
        setDateUpdateStocks("");
        setIsVisibleBtnUpdate(true);
        setBtnTitle("Завантажте залишки.");

    }

    const handleClose = ()=>{
        window.close();
    }

    const handleToForward = async ()=>{
        // console.log(getKeysFromLocalStorageByStartsWith("stocks").length>0)
        if(getKeysFromLocalStorageByStartsWith("stocks").length>0){
            navigate("pricelist",{replace:true});
        }
    }
   
    return (
        
        <div className="row mx-auto">
                {<DecodeData show= {modalShow} 
                onHide = {()=>setModalShow(false)}
                setIsVisibleBtnUpdate={setIsVisibleBtnUpdate}
                setDateUpdateStocks={setDateUpdateStocks}
                setIsVisibleBtnToForward={setIsVisibleBtnToForward}
                setShowClearLocalStorage={setShowClearLocalStorage}/>}

            <div className="col mx-auto text-center center-block">
                
                <div className="my-2 w-75 mx-auto">
                    <input type="image" src={logo192} alt="logo" className="rounded-3 shadow-sm my-3" height="200px" width="200px" />
                </div>
                {isLoading?<Preloader/>:null}
                {dateUpdateStocks&&<span className="text-info fst-italic fs-6">{"Залишки станом на "+dateUpdateStocks}</span>}
                    <div className="row mx-auto">
                        <div className="col mx-auto text-center center-block">
                            <div className="my-3">
                            {isVisibleBtnUpdate&&<button  onClick={updateStocks} type="submit" className="rounded-3 shadow-sm border border-success fs-1 "><span className="text-danger fs-5 lh-1">{btnTitle}</span></button>}
                            </div>
                            <div className="my-3">
                                {isVisibleBtnToForward&&<button  onClick={handleToForward} type="button" className="rounded-3 shadow-sm border border-success fs-1">Вперед</button>}
                            </div>
                            <div className="my-3">
                              {showClearLocalStorage?<button  onClick={clearLocalStorage} type="submit" className="rounded-pill shadow-sm border text-danger fst-italic border-danger py-2 fs-4">Clear LocalStorage</button> :null}  
                            </div>
                            {/* <div className="my-2">
                                <button  onClick={handleClose} type="submit" className="rounded-3 shadow-sm border border-success fs-1">Выйти</button>
                            </div> */}

                        </div> 
                    </div>
            </div>   
        </div> 
    )  

}
export {Homepage}
